*, *::before, *::after {
    box-sizing: border-box;
  }
  * {
    margin: 0;
  }
  body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
  }
  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }
  input, button, textarea, select {
    font: inherit;
  }
  p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
  }
  #root, #__next {
    isolation: isolate;
  }


/*holy grail format*/
.wrapper {
    z-index: 0;
    display: grid;
    grid-template-columns:
      1fr
      min(65ch, 100%)
      1fr;
  }
  .wrapper > * {
    display: grid;
    grid-column: 2;
    grid-template-columns: 1fr min(5000px, calc(100% - 70px)) 1fr;
    
  }
  .full-bleed {
    width: 100%;
    grid-column: 1 / -1;
  }


